/* ==========================================================================
   Author's custom styles
   ========================================================================== */
html,
body {
  height: 100%;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}
.container {
  min-height: 100%;
  display: flex;
  width: 500vw;
}
.container > div {
  width: 100vw;
  z-index: 10;
}
#home {
  height: 100vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#home div:first-child {
  flex: 1;
  padding: 50px;
}
#home div:last-child {
  flex: 2;
  margin-top: 50px;
}
#home #logo {
  width: 100%;
  height: auto;
  max-width: 478px;
  margin-top: 50px;
  margin-bottom: 24px;
}
#home p {
  max-width: 478px;
  margin-bottom: 46px;
}
#home p:last-child {
  margin-bottom: 0;
}
#home ul {
  margin: 0;
  padding: 0;
}
#home ul li {
  height: 164px;
  line-height: 3.2em;
  padding-left: 1.25em;
  font-size: 48px;
  font-weight: 800;
  text-transform: capitalize;
  margin-bottom: 30px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  width: 100%;
}
#home ul li:nth-child(1) {
  background: #4b8864;
}
#home ul li:nth-child(2) {
  background: #feefa0;
}
#home ul li:nth-child(3) {
  background: #fcaf2d;
}
#home ul li:nth-child(4) {
  background: #b34924;
  margin-bottom: 0;
}
#home ul li:hover {
  transform: translateX(-50px);
  width: 100%;
}
#home ul li:active {
  opacity: .6;
}
#home ul li:after {
  content: '';
  float: right;
  margin-right: 35px;
  border-top: 25px solid transparent;
  border-left: 25px solid #000000;
  border-bottom: 25px solid transparent;
  margin-top: 1.2em;
}
.section h1 {
  color: white;
  text-transform: capitalize;
  font-size: 96px;
  margin: 0;
  display: inline-block;
  height: 100vh;
  padding-top: 50px;
  text-indent: 50px;
}
.section .content {
  position: absolute;
  top: 0;
  margin-left: 25vw;
  margin-top: 25vh;
  height: 60vh;
  overflow: hidden;
}
.section p {
  color: black;
  display: inline-block;
  width: 100vw;
  padding: 0 25px;
}
.section.col-1 h1 {
  width: 25vw;
}
.section.col-1 .content {
  margin-left: 25vw;
  column-count: 3;
}
.section.col-1 p {
  width: 21vw;
}
.section.col-2 h1 {
  width: 50vw;
}
.section.col-2 .content {
  margin-left: 50vw;
  column-count: 2;
}
.section.col-2 p {
  width: 21vw;
}
.section.col-3 h1 {
  width: 75vw;
}
.section.col-3 .content {
  margin-left: 75vw;
  column-count: 1;
}
.section.col-3 p {
  width: 21vw;
}
#about {
  height: 100vh;
}
#about h1 {
  background: #4b8864;
}
#technology {
  height: 100vh;
}
#technology h1 {
  background: #feefa0;
}
#content {
  height: 100vh;
}
#content h1 {
  background: #fcaf2d;
}
#news {
  height: 100vh;
}
#news h1 {
  background: #b34924;
}
footer {
  width: 100vw;
  height: 100px;
  position: fixed;
  bottom: 0;
  background: #dadada;
  z-index: 10;
}
footer .icon-home {
  width: 64px;
  height: 64px;
  background: #f00;
  display: block;
  background: url('../img/home.png') no-repeat center;
  margin: 20px 20px;
  cursor: pointer;
}
footer .icon-home:hover {
  opacity: .5;
}
