// variables

@color1: #4b8864;
@color2: #feefa0;
@color3: #fcaf2d;
@color4: #b34924;

@font: 'Helvetica Neue', Helvetica, Arial, sans-serif;

@menu-item-height: 164px;

html,
body {
    height: 100%;
    font-family: @font;
    -webkit-font-smoothing: antialiased;
//    overflow: hidden;
}

.container {
    min-height: 100%;
    display: flex;
    width: 500vw;

    & > div {
        width: 100vw;
        z-index: 10;
    }
}

#home {
    @home-column-width: 478px;

    height: 100vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    div {
        &:first-child {
            flex: 1;
            padding: 50px;
        }
        &:last-child {
            flex: 2;
            margin-top: 50px;
        }
    }

    #logo {
        width: 100%;
        height: auto;
        max-width: @home-column-width;
//        height: 242px;
        margin-top: 50px;
        margin-bottom: 24px;
    }

    p {
        max-width: @home-column-width;
        margin-bottom: 46px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    ul {
        margin: 0;
        padding: 0;

        li {
            height: @menu-item-height;
            line-height: 3.2em;
            padding-left: 1.25em;
            font-size: 48px;
            font-weight: 800;
            text-transform: capitalize;
            margin-bottom: 30px;
            transition: all .2s ease-in-out;
            cursor: pointer;
            width: 100%;

            &:nth-child(1) {
                background: @color1;
            }
            &:nth-child(2) {
                background: @color2;
            }
            &:nth-child(3) {
                background: @color3;
            }
            &:nth-child(4) {
                background: @color4;
                margin-bottom: 0;
            }

            &:hover {
                transform: translateX(-50px);
                width: 100%;
            }

            &:active {
                opacity: .6;
            }

            &:after {
                @triangle-width: 25px;

                content: '';
                float: right;
                margin-right: 35px;
                border-top: @triangle-width solid transparent;
                border-left: @triangle-width solid black;
                border-bottom: @triangle-width solid transparent;
                margin-top: 1.2em;
            }
        }
    }
}

.section {
    h1 {
        color: white;
        text-transform: capitalize;
        font-size: 96px;
        margin: 0;
        display: inline-block;
        height: 100vh;
        padding-top: 50px;
        text-indent: 50px;
    }

    .content {
        position: absolute;
        top: 0;
        margin-left: 25vw;
        margin-top: 25vh;
        height: 60vh;
        overflow: hidden;
    }

    p {
        color: black;
        display: inline-block;
        width: 100vw;
        padding: 0 25px;
    }

    &.col-1 {
        @width: 25vw;
        @paragraph-width: 97 - @width;

        h1 {
            width: @width;
        }

        .content {
            margin-left: @width;
            column-count: 3;
        }

        p {
            width: 21vw;
        }
    }

    &.col-2 {
        @width: 50vw;
        @paragraph-width: 97vw - @width;

        h1 {
            width: @width;
        }

        .content {
            margin-left: @width;
            column-count: 2;
        }

        p {
            width: 21vw;
        }
    }

    &.col-3 {
        @width: 75vw;
        @paragraph-width: 97 - @width;

        h1 {
            width: @width;
        }

        .content {
            margin-left: @width;
            column-count: 1;
        }

        p {
            width: 21vw;
        }
    }
}

#about {
    height: 100vh;

    h1 {
        background: @color1;
    }
}

#technology {
    height: 100vh;

    h1 {
        background: @color2;
    }
}

#content {
    height: 100vh;

    h1 {
        background: @color3;
    }
}

#news {
    height: 100vh;

    h1 {
        background: @color4;
    }
}

footer {
    width: 100vw;
    height: 100px;
    position: fixed;
    bottom: 0;
    background: #dadada;
    z-index: 10;

    .icon-home {
        @icon-width: 64px;

        width: @icon-width;
        height: @icon-width;
        background: #f00;
        display: block;
        background: url('../img/home.png') no-repeat center;
        margin: 20px 20px;
        cursor: pointer;

        &:hover {
            opacity: .5;
        }
    }
}